:root {
    --main-violet: #9B79B5;
    --main-blue: #2B4999;
    --main-orange: #F4974B;
}

.chat-list {
    height: 80%;
    width: 20vw;
    border-right: solid 1px var(--main-violet);
    box-shadow: 4px 0px 20px -10px rgba(0, 0, 0, 0.30);
    list-style: none;
    position: fixed;
    top: 100px;
    bottom: 200px;
}

.list-item {
    width: 20vw;
    overflow-y: scroll;
    height: 100%;
}

.list-item::-webkit-scrollbar {
    width: 5px;
    height: 8px;
    background-color: #e6cafb;
    border-radius: 0 0 99px 99px;
}

.list-item::-webkit-scrollbar-thumb {
    background: var(--main-violet);
    border-radius: 99px;
}


.chat-list-header {
    padding: 20px;
    background-color: var(--main-violet);
    text-align: center;
}

.chat-list-header h4 {
    margin: 0;
}

.chat-item {
    display: flex;
    border-bottom: solid 1px var(--main-violet);
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    height: 95px;
    padding: 18px;
    cursor: pointer;
    background-color: #fdfdfd;
}

.chat-item:hover {
    background-color: #9b79b515;
    font-weight: 600;
}

.chat-item-content {
    display: flex;
    flex-direction: column;
    height: 48px;
    width: 80%;

}

.chat-item-content span {
    height: 25px;
    width: 90%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.last-message {
    text-overflow: ellipsis;
    font-weight: 300;
}


.profile-photo img {
    height: 58px;
    width: 58px;
    border-radius: 50%;
}

.chat-window {
    position: fixed;
    height: 400px;
    width: 350px;
    border: solid 2px var(--main-violet);
    right: 15px;
    bottom: 83px;
    border-radius: 5px 5px 0 0;
    background-color: #fdfdfd;

}



.close-chat-window {
    position: absolute;
    left: 88%;
    top: 2%;
    height: 31px;
    width: 21px;
    padding: 0;
    border: none;
    background-color: #fdfdfd;
    color: var(--main-violet);
    font-size: 28px;
    font-weight: 500;
}

.chat-window-header {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 2px 5px;
    border-bottom: solid 1px var(--main-violet);
    height: 15%;
    border-radius: 5px 5px 0 0;

}

.chat-window-header .profile-photo img {
    height: 48px;
    width: 48px;
    border-radius: 50%;
}

.message-zone {
    display: flex;
    flex-direction: column-reverse;
    height: 71%;
    border-bottom: solid 1px var(--main-violet);
    overflow-y: scroll;

}

.message-zone::-webkit-scrollbar {
    width: 5px;
    height: 8px;
    background-color: #e6cafb;
    border-radius: 99px;
}

.message-zone::-webkit-scrollbar-thumb {
    background: var(--main-violet);
    border-radius: 99px;
}

.message-item {
    /* background-color: #F4974B; */
    display: flex;
    align-items: center;
    gap: 10px;
    min-height: 28%;
    padding: 5px 5px;
}

.sender {
    justify-content: flex-end;
}

.message {
    background-color: var(--main-violet);
    border-radius: 6px;
    padding: 5px 15px;
}

.message-sender {
    background-color: var(--main-orange);
    border-radius: 6px;
    padding: 5px 15px;
}

.message-item .profile-photo img {

    height: 40px;
    width: 40px;
}

.text-zone {
    display: flex;
    align-items: center;
    justify-content: center;
}

.text-zone input {
    margin-top: 2px;
    width: 74%;
    height: 49px;
    border: none;
    padding-left: 5px;
    font-size: 20px;
}

.text-zone input:focus {
    border: none;
}

.text-zone button {
    background-color: var(--main-violet);
    color: #fdfdfd;
    margin-top: 1px;
    margin-right: 2px;
    /* font-family: NE PAS OUBLIER ; */
}

.text-zone button:hover {
    background-color: #7d46a7;
    /* font-family: NE PAS OUBLIER ; */
}

@media screen and (max-width: 1470px) {
    .chat-window {
        right: 2%;
        bottom: 85px;
    }
}

@media screen and (max-width: 1470px) {
    .chat-window {
        right: 2%;
        bottom: 85px;
    }
}

@media screen and (max-width: 970px) {
    .chat-item-content {
        display: none;
    }
}

@media screen and (max-width: 660px) {
    .chat-list-header {
        padding: 10px 5px 10px;
    }

    .chat-list-header h4 {
        margin-bottom: 0;
        font-size: 15px;
    }
}

@media screen and (max-width: 360px) {


    .private-home .chat-list {
        width: 25vw;
    }

    .private-home .list-item {
        width: 25vw;
    }

    .chat-item .chat-item-content {
        display: none;
    }

    .chat-window {
        left: 0;
        bottom: 104px;
    }

}