:root {
    --main-violet: #9B79B5;
    --main-violet-hover: #8448b3;
    --main-blue: #2B4999;
    --main-orange: #F4974B;
  }

.format {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    padding: 60px 200px;
}

.format h1{
    text-align: center;
}
.format h2{
    text-align: center;
    margin-bottom: 50px;
}
.format h3{
    margin: 20px 0;
    text-transform: uppercase;
}

.violet{
    color: var(--main-violet);
}

@media screen and (max-width: 360px) {
    .format {
        padding: 60px 25px;
    }

    .format h1{
       font-size: 40px;
    }

}